* {
  box-sizing: border-box;
}

.dark {
  background-color: #292c35;
  color: cornsilk;
}

.label {
  background-color: #111;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
  border-radius: 50px;
  position: relative;
  height: 26px;
  width: 50px;
  transform: scale(1.5);
}

.ball {
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  height: 22px;
  width: 22px;
  top: 2px;
  left: 2px;
  transition: transform 0.2s linear;
}

.checkbox {
  opacity: 0;
  position: absolute;
}

.checkbox:checked + .label .ball {
  transform: translateX(24px);
}
